.hero_section {
  display: flex;
  text-align: center;
  align-items: center;
  background: #B88746;
}

.hero_title {
  width: 50%;
}

.section-one {
  height: 100vh;
}

.imgContainer {
  justify-content: center;
  align-items: center;
}

.react-parallax {
  height: 100vh;
  width: 80%;
}

.homepage_parallax {
  height: 100vh;
  width: 50%;
}

.react-parallax-bgimage {
  height: 100vh !important;
  object-fit: cover;
  top: 34px;
}

.homepageImage {
  background-position: center;
  display: flex;
  background-size: cover;
  width: 100%;
  height: 100vh;
  justify-content: center;
  height: auto;
  align-items: center;
}

.image-wrapper-home {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-top: 56px;
  height: 100vh;
}

.logo-image-wrapper {
  text-align: center;
  padding: 0px 0px 0px 0px;
}

.logo-image {
  width: 100%;
}

.mission-header {
  border-bottom: solid;
  color: #013240;
}

.about-text-container {
  z-index: 100;
}

.brianna-name-title {
  padding: 30px 20px 30px 20px;
  text-align: center;
}

.qualification-container {
  border-bottom: solid 1px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

